body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Poppins' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#main_header{
  width: 100%;
  height: 80vh;
  margin-top: 50px;
}

.navbar-bg{
  background-color: teal;
  /* box-shadow: 5px 5px 50px -15px rgba(0, 0, 0, 0.5); */
}

.active_menu{

  font-weight: bold;
  border-bottom: 2px solid whitesmoke;
}

.navbar-brand{

  font-weight: bold;
  letter-spacing: 2px;
}

.navbar a{

  font-size: 0.8rem;
  text-transform: capitalize;
  font-weight: 600;
  color: whitesmoke !important;
}

#main_header .brand_name{

  color: teal;
}

#main_header h1{

  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
}

#main_header h2{

  font-size: 24px;
  font-weight: 500;
}

.btn-get-started{
  padding: 10px 30px 10px 30px;
  text-decoration: none;
  color: white;
  background-color: teal;
  border-radius: 50px;
  letter-spacing: 1px;
  font-weight: bold;
  transition: 0.3s;
  border: 2px solid teal;
  font-size: 14px;
}

.btn-get-started:hover{
  color: teal;
  background-color: white;
  border: 2px solid teal;
}

#main_header .animate{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down{
  0% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(-5px);
  }
}

/* SERVICES PAGE */

#services h1{

  font-weight: bold;
  font-size: 48px;
}

.card-title{

  font-weight: bold !important;
}

.card-text{

  font-size: 15px !important;
}

.card-body a{
  font-size: 12px;
  padding: 5px 15px 5px 15px;
}

/* CONTACT PAGE */

.contact_heading h1{

  font-weight: bold;
}

.contact_div p{

  font-size: 18px;
  font-weight: bold;
  color: teal;
}

/* FOOTER */

.footer-bg{
  
  border-top: 1px solid #efefef;
}

.footer-bg p{
  
  font-size: 12px;
}

/* RESPONSIVE CODE */

@media (max-width: 991px){
  #main_header{
    height: 80vh;
    /* text-align: center; */
    margin-top: 32px;
  }
  #main_header .header_img{
    text-align: center !important;
  }
  #main_header .header_img img{
    width: 60% !important;
  }
}

@media (max-width: 768px){
  #main_header{
    margin-top: 20px;
  }
  #main_header h1{
    font-size: 28px;
  }
  #main_header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  #main_header .header_img img{
    width: 70%;
  }
}